.about{
    height: 90vh;
    padding: 2rem 0rem;
    font-family: "Readex-Pro", sans-serif;
    background-color: var(--black-body);
}
.about h1{
    color: var(--white);
    text-align: center;
    padding-bottom: 2rem;
}
.about-flex{
    display: flex;
}
.about .left-content {
    width: 50%;
}
.about .right-content {
    width: 40%;
}

.about .left-content img{
    display: flex;
    width: 70%;
    height: 70%;
    padding: 0px 0px;
    position:relative;
    animation-duration: 1s;
    animation-name: animation1;
    animation-iteration-count: infinite;
}

.about .right-content p{
    color: var(--white);
    text-align: left;
    line-height: 1.5rem;
    padding:2rem 0;
}
.about .right-content h2{
    color: var(--white);
    font-size: 1.7rem;
    padding-top: 3rem;
}
.about .right-content a{
    color: var(--white);
    font-size: 1.5rem;
    font-weight: bold;
    padding: 0.8rem 1.5rem;
    border-radius: 0.3rem;
    border: 0.15rem solid var(--aqua);
    text-decoration: none;
    list-style: none;
}
.about .right-content a:hover{
    color: var(--black-high);
    background: var(--aqua);
    font-weight: bold;
}

@media only screen and (max-width: 1000px) {
    .about{
        padding: 2rem 2rem;
        height: 90vmax;
    }
    .about-flex{
        display: block;
    }
    .about .left-content {
        width: 100%;
    }
    .about .right-content {
        width: 100%;
    }
    .about .left-content img{
        display: flex;
        width: 50%;
        height: 50%;
        margin: auto;
    }
    .about .right-content p{
        text-align: center;
    }
    .about .right-content h2{
        text-align: center;
    }
    .about .right-content a{
        margin-left: 42%;
        margin-bottom: 5%;
        border-radius: 0.3rem;
        border: 0.15rem solid var(--aqua);
        text-decoration: none;
        list-style: none;
    }
  }
@media only screen and (max-width: 750px) {
    .about .right-content a{
        margin-left: 40%;
    }
    .about .right-content p{  
        font-size: 1.2rem;
    }
    .about .right-content h1{
        font-size: 3.5rem;
    }

    .about .right-content .left-content a{
        font-size: 1.2rem;
        padding: 0.6rem 1rem;
        border-radius: 0.2rem;
    }
  }
@media only screen and (max-width: 550px) {
    .about .right-content a{
        margin-left: 34%;
    }
    .about .right-content p{  
        font-size: 1.2rem;
    }
    .about .right-content h1{
        font-size: 3.5rem;
    }

    .about .right-content .left-content a{
        font-size: 1.2rem;
        padding: 0.6rem 1rem;
        border-radius: 0.2rem;
    }
  }
@media only screen and (max-width: 400px) {
    .about .right-content a{
        margin-left: 28%;
    }
    .about .right-content p{  
        font-size: 1.1rem;
    }
    .about .right-content h1{
        font-size: 3.2rem;
    }
    .about .right-content h2{
        font-size: 1.6rem;
    }
    .about .right-content .left-content a{
        font-size: 1rem;
        padding: 0.4rem 0.9rem;
        border-radius: 0.2rem;
    }
  }
@media only screen and (max-width: 300px) {
    .about .right-content a{
        margin-left: 22%;
    }
    .about .right-content p{  
        font-size: 1.1rem;
    }
    .about .right-content h1{
        font-size: 3.2rem;
    }
    .about .right-content h2{
        font-size: 1.6rem;
    }
    .about .right-content .left-content a{
        font-size: 1rem;
        padding: 0.4rem 0.9rem;
        border-radius: 0.2rem;
    }
  }



  @keyframes animation1 {
    0%   {top:0px; bottom: 0;}
    50%  {top:-10px; bottom: -10px;}
  }