.home{
    height: 90vh;
    padding: 7rem 4.5rem;
    font-family: "Readex-Pro", sans-serif;
}
.home-flex{
    display: flex;
}
.home .left-content{
    width: 40%;
}
.home .right-content{
    position: absolute;
    right: 20%;
    top: 50%;
    width: 30%;
    z-index: -9;
}
.home p{
    color:var(--white);   
    font-size: 1.6rem;
    padding-left: 0.2rem;
}
.home h1{
    padding: 0.2rem 0;
    color: var(--white);
    font-size: 4.5rem;
}
.home h2{
    color: var(--white);
    font-size: 2.2rem;
    padding-bottom: 40px;
}
.home h2 span{
    color: var(--aqua);
}
.home .left-content a{
    color: var(--white);
    font-size: 1.5rem;
    font-weight: bold;
    padding: 0.8rem 1.5rem;
    border-radius: 0.3rem;
    border: 0.15rem solid var(--aqua);
    text-decoration: none;
    list-style: none;
}
.home .left-content a:hover{
    color: var(--black-high);
    background: var(--aqua);
    font-weight: bold;
}

@media only screen and (max-width: 1000px) {
    .home{
        padding: 0rem 0rem;
        height: 90vmax;
    }
    .home-flex{
        display: block;
    }
    .home .left-content{
        width: 100%;
        padding-left: 2rem;
        padding-top: 7vmax;
        background-color: var(--black-body);
    }
    .home .right-content{
        position: absolute;
        right: 30%;
        top: 70vmax;
        width: 30%;
    }
  }
@media only screen and (max-width: 750px) {
    .home p{  
        font-size: 1.2rem;
    }
    .home h1{
        font-size: 3.5rem;
    }
    .home h2{
        font-size: 1.7rem;
    }
    .home .left-content a{
        font-size: 1.2rem;
        padding: 0.6rem 1rem;
        border-radius: 0.2rem;
    }
  }
@media only screen and (max-width: 400px) {
    .home p{  
        font-size: 1.1rem;
    }
    .home h1{
        font-size: 3.2rem;
    }
    .home h2{
        font-size: 1.6rem;
    }
    .home .left-content a{
        font-size: 1rem;
        padding: 0.4rem 0.9rem;
        border-radius: 0.2rem;
    }
  }

@media only screen and (max-width: 300px) {
    .home .right-content{
        right: 40%;
    }
  }